.card {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-bottom: 10px;
  border-radius: 8px;
  box-shadow: $box-shadow-value;
  opacity: 1;
  overflow: hidden;
  cursor: pointer;

  &-image {
    align-self: center;
    position: relative;
    width: 100%;
    max-width: 200px;

    img {
      display: block;
      width: 100% !important;
      height: auto !important;
    }
  }

  .tooltip {
    position: absolute;
    right: 0;
    top: 0;
    margin-top: 10px;
    margin-right: 10px;
    background-color: $red-accent;
    font-size: 12px;
    font-weight: bold;
    color: #fff;
    padding: 0 12px;
    border-radius: 5px;
  }

  &-body {
    padding: 10px;
    position: relative;

    h5 {
      font-size: 14px;
      font-size: clamp(12px, calc(1vw + 1px), 14px);
      font-weight: 500;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    p {
      font-size: 14px;
      font-size: clamp(12px, calc(1vw + 1px), 14px);

      span {
        font-weight: bold;
      }
    }

    .card-description {
      display: grid;
      position: relative;
      top: 12px;

      .discount {
        font-size: 12px;
        font-size: clamp(11px, calc(1vw), 12px);
        color: $grey-text;
        text-decoration: line-through;
      }

      .price {
        position: absolute;
        right: 0;
        font-size: 14px;
        font-size: clamp(12px, calc(1vw + 1px), 14px);
        font-weight: bold;
        color: $red-accent;
      }
    }
  }
}